    display: flex;
    width: 100%;
    align-items: center
}
.iQydii .logo {
    width: 20%;
}
.iQydii .logo .icon {
    background-image: url(https://frontend.tikicdn.com/_desktop-next/static/img/sprite@2x.png);
    background-size: 404px;
